#heroDiv {
  width: 100%;
  height: 300px;
  color: #000;
  background: #ccc url("pokemonBackground.jpg");
  background-size: 100% 100%;
  padding: 100px 0;
  margin-bottom: 50px;
}
#textDiv {
  background: rgba(51, 235, 161, 0.6);
